'use client';

import { ReactNode } from 'react';
import { motion } from 'framer-motion';
import { useInView } from '@/hooks/useInView';
import { LucideIcon } from 'lucide-react';

interface AnimatedContentSectionProps {
  title?: string;
  icon?: LucideIcon;
  iconColor?: string;
  children: ReactNode;
  delay?: number;
  animation?: 'fadeUp' | 'fadeLeft' | 'fadeRight' | 'scale' | 'bounce';
  className?: string;
}

export default function AnimatedContentSection({
  title,
  icon: Icon,
  iconColor = 'text-pink-500',
  children,
  delay = 0,
  animation = 'fadeUp',
  className = 'mb-12 bg-white rounded-xl p-8 shadow-sm'
}: AnimatedContentSectionProps) {
  const { ref, isInView } = useInView({ threshold: 0.2 });

  const animations = {
    fadeUp: {
      hidden: { y: 50, opacity: 0 },
      visible: { y: 0, opacity: 1 }
    },
    fadeLeft: {
      hidden: { x: -50, opacity: 0 },
      visible: { x: 0, opacity: 1 }
    },
    fadeRight: {
      hidden: { x: 50, opacity: 0 },
      visible: { x: 0, opacity: 1 }
    },
    scale: {
      hidden: { scale: 0.8, opacity: 0 },
      visible: { scale: 1, opacity: 1 }
    },
    bounce: {
      hidden: { y: 50, opacity: 0 },
      visible: { 
        y: 0, 
        opacity: 1,
        transition: {
          type: "spring",
          bounce: 0.4,
          duration: 1
        }
      }
    }
  };

  const selectedAnimation = animations[animation];

  return (
    <motion.section
      ref={ref}
      initial="hidden"
      animate={isInView ? "visible" : "hidden"}
      variants={selectedAnimation}
      transition={{ 
        duration: 0.5, 
        delay,
        ease: "easeOut"
      }}
      className={`${className} overflow-hidden relative`}
    >
      {/* Hover effect */}
      <motion.div 
        className="absolute inset-0 bg-pink-500/5 rounded-xl opacity-0"
        whileHover={{ opacity: 1 }}
        transition={{ duration: 0.3 }}
      />

      {/* Content */}
      <div className="relative z-10">
        {(title || Icon) && (
          <div className="flex items-center mb-6">
            {Icon && <Icon className={`h-8 w-8 ${iconColor} mr-3`} />}
            {title && <h2 className="text-3xl font-bold text-gray-900">{title}</h2>}
          </div>
        )}
        <div className="space-y-4 text-gray-700">
          {children}
        </div>
      </div>

      {/* Corner decoration */}
      <motion.div 
        className="absolute top-0 right-0 w-16 h-16 bg-gradient-to-br from-transparent to-pink-100 rounded-bl-3xl rounded-tr-xl"
        initial={{ opacity: 0, scale: 0 }}
        animate={{ opacity: isInView ? 1 : 0, scale: isInView ? 1 : 0 }}
        transition={{ delay: delay + 0.3, duration: 0.5 }}
      />
    </motion.section>
  );
} 