'use client';

import { useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { motion } from 'framer-motion';
import { Filter, Search, Tag, Package, Check, X } from 'lucide-react';
import AnimatedPageHeader from '@/components/AnimatedPageHeader';
import AnimatedContentSection from '@/components/AnimatedContentSection';

// Временные данные для категорий
const categories = [
  { id: 1, name: 'Воздушные шары', count: 124, image: '/images/categories/balloons.jpg' },
  { id: 2, name: 'Композиции', count: 57, image: '/images/categories/compositions.jpg' },
  { id: 3, name: 'Фигуры из шаров', count: 34, image: '/images/categories/figures.jpg' },
  { id: 4, name: 'Декор для праздника', count: 82, image: '/images/categories/decor.jpg' },
  { id: 5, name: 'Гелий и аксессуары', count: 28, image: '/images/categories/helium.jpg' },
  { id: 6, name: 'Готовые решения', count: 46, image: '/images/categories/solutions.jpg' },
];

// Временные данные для продуктов
const products = [
  { id: 1, name: 'Воздушный шар "С Днем Рождения"', price: 350, image: '/images/products/balloon1.jpg', isNew: true, isHit: false },
  { id: 2, name: 'Набор шаров "Праздник"', price: 1200, image: '/images/products/balloon-set.jpg', isNew: false, isHit: true },
  { id: 3, name: 'Фольгированная цифра "3"', price: 550, image: '/images/products/number.jpg', isNew: false, isHit: false },
  { id: 4, name: 'Композиция "Сюрприз"', price: 2400, image: '/images/products/composition.jpg', isNew: true, isHit: true },
  { id: 5, name: 'Фигура из шаров "Мишка"', price: 1800, image: '/images/products/bear.jpg', isNew: false, isHit: false },
  { id: 6, name: 'Шары "Сердца" набор 10 шт', price: 890, image: '/images/products/hearts.jpg', isNew: false, isHit: false },
  { id: 7, name: 'Шар-сюрприз гигант', price: 3200, image: '/images/products/surprise.jpg', isNew: true, isHit: false },
  { id: 8, name: 'Гирлянда из шаров', price: 1500, image: '/images/products/garland.jpg', isNew: false, isHit: true },
];

const filters = [
  { id: 'color', name: 'Цвет', options: ['Красный', 'Синий', 'Зеленый', 'Розовый', 'Золотой'] },
  { id: 'size', name: 'Размер', options: ['Маленький', 'Средний', 'Большой', 'Гигант'] },
  { id: 'material', name: 'Материал', options: ['Латекс', 'Фольга', 'Хром'] },
  { id: 'event', name: 'Событие', options: ['День рождения', 'Свадьба', 'Юбилей', 'Выпускной'] },
];

export default function CatalogPage() {
  const [activeFilters, setActiveFilters] = useState<string[]>([]);
  const [showFilters, setShowFilters] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  const toggleFilter = (filter: string) => {
    setActiveFilters(prev => 
      prev.includes(filter) 
        ? prev.filter(f => f !== filter) 
        : [...prev, filter]
    );
  };

  // Анимационные варианты
  const staggerContainer = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: { 
      y: 0, 
      opacity: 1,
      transition: { type: "spring", stiffness: 300, damping: 24 } 
    }
  };

  return (
    <div className="container mx-auto px-4 py-16">
      <AnimatedPageHeader
        title="Каталог товаров"
        description="Все для вашего праздника в одном месте"
        icon={Package}
        bgColor="bg-pink-100"
        iconColor="text-pink-500"
      />

      {/* Поиск и фильтры */}
      <AnimatedContentSection
        delay={0.1}
        animation="fadeUp"
        className="mb-8 p-6"
      >
        <div className="flex flex-col md:flex-row gap-4">
          {/* Поиск */}
          <div className="relative flex-1">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <Search className="h-5 w-5 text-gray-400" />
            </div>
            <motion.input
              whileFocus={{ scale: 1.01 }}
              transition={{ type: "spring", stiffness: 300, damping: 10 }}
              type="text"
              placeholder="Поиск товаров..."
              className="pl-10 pr-4 py-3 w-full border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-pink-500 outline-none"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          
          {/* Кнопка фильтров */}
          <motion.button
            whileHover={{ scale: 1.03 }}
            whileTap={{ scale: 0.98 }}
            onClick={() => setShowFilters(!showFilters)}
            className="flex items-center justify-center px-4 py-3 bg-pink-500 text-white rounded-lg font-medium"
          >
            <Filter className="h-5 w-5 mr-2" />
            {showFilters ? 'Скрыть фильтры' : 'Показать фильтры'}
          </motion.button>
        </div>

        {/* Фильтры */}
        <motion.div 
          initial={{ height: 0, opacity: 0 }}
          animate={{ 
            height: showFilters ? 'auto' : 0,
            opacity: showFilters ? 1 : 0
          }}
          transition={{ duration: 0.3 }}
          className="overflow-hidden mt-4"
        >
          <div className="border border-gray-200 rounded-lg p-4 bg-gray-50">
            <div className="flex flex-wrap gap-4">
              {filters.map((filter) => (
                <div key={filter.id} className="flex flex-col">
                  <h3 className="font-medium text-gray-700 mb-2">{filter.name}</h3>
                  <div className="flex flex-wrap gap-2">
                    {filter.options.map((option) => {
                      const isActive = activeFilters.includes(option);
                      return (
                        <motion.button
                          key={option}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                          onClick={() => toggleFilter(option)}
                          className={`px-3 py-1 rounded-full text-sm ${
                            isActive 
                              ? 'bg-pink-500 text-white' 
                              : 'bg-white text-gray-700 border border-gray-300'
                          } transition-colors`}
                        >
                          {option}
                          {isActive && <Check className="h-3 w-3 ml-1 inline" />}
                        </motion.button>
                      );
                    })}
                  </div>
                </div>
              ))}
            </div>
            
            {activeFilters.length > 0 && (
              <div className="mt-4 pt-4 border-t border-gray-200">
                <div className="flex items-center justify-between">
                  <p className="text-sm text-gray-600">Активные фильтры:</p>
                  <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    onClick={() => setActiveFilters([])}
                    className="text-sm text-pink-500 font-medium"
                  >
                    Сбросить все
                  </motion.button>
                </div>
                <div className="flex flex-wrap gap-2 mt-2">
                  {activeFilters.map((filter) => (
                    <motion.span
                      key={filter}
                      initial={{ opacity: 0, scale: 0.8 }}
                      animate={{ opacity: 1, scale: 1 }}
                      exit={{ opacity: 0, scale: 0.8 }}
                      className="flex items-center bg-pink-100 text-pink-800 px-2 py-1 rounded-full text-sm"
                    >
                      {filter}
                      <button
                        onClick={() => toggleFilter(filter)}
                        className="ml-1 text-pink-500 hover:text-pink-700"
                      >
                        <X className="h-3 w-3" />
                      </button>
                    </motion.span>
                  ))}
                </div>
              </div>
            )}
          </div>
        </motion.div>
      </AnimatedContentSection>

      {/* Категории */}
      <AnimatedContentSection
        title="Категории"
        icon={Tag}
        delay={0.2}
        animation="fadeUp"
        className="mb-12"
      >
        <motion.div 
          variants={staggerContainer}
          initial="hidden"
          animate="visible"
          className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6"
        >
          {categories.map((category, index) => (
            <motion.div 
              key={category.id}
              variants={itemVariants}
              whileHover={{ y: -5, scale: 1.02 }}
              transition={{ type: "spring", stiffness: 300, damping: 10 }}
              className="relative overflow-hidden bg-white rounded-xl shadow-sm"
            >
              <Link href={`/catalog/${category.id}`} className="block h-full">
                <div className="relative h-48 overflow-hidden">
                  <Image
                    src={category.image}
                    alt={category.name}
                    fill
                    sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                    className="object-cover transition-transform duration-500 hover:scale-105"
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent pointer-events-none" />
                </div>
                <div className="p-4">
                  <h3 className="text-xl font-bold mb-1">{category.name}</h3>
                  <p className="text-gray-500 text-sm">{category.count} товаров</p>
                </div>
              </Link>
            </motion.div>
          ))}
        </motion.div>
      </AnimatedContentSection>

      {/* Популярные товары */}
      <AnimatedContentSection
        title="Популярные товары"
        delay={0.3}
        animation="fadeUp"
      >
        <motion.div 
          variants={staggerContainer}
          initial="hidden"
          animate="visible"
          className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6"
        >
          {products.map((product, index) => (
            <motion.div 
              key={product.id}
              variants={itemVariants}
              whileHover={{ y: -5 }}
              transition={{ type: "spring", stiffness: 300, damping: 10 }}
              className="bg-white rounded-xl overflow-hidden shadow-sm group"
            >
              <Link href={`/products/${product.id}`} className="block h-full">
                <div className="relative h-56 overflow-hidden">
                  <Image
                    src={product.image}
                    alt={product.name}
                    fill
                    sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 25vw"
                    className="object-cover transition-transform duration-500 group-hover:scale-105"
                  />
                  {product.isNew && (
                    <span className="absolute top-2 left-2 bg-green-500 text-white text-xs font-bold px-2 py-1 rounded-full">
                      Новинка
                    </span>
                  )}
                  {product.isHit && (
                    <span className="absolute top-2 right-2 bg-orange-500 text-white text-xs font-bold px-2 py-1 rounded-full">
                      Хит продаж
                    </span>
                  )}
                </div>
                <div className="p-4">
                  <h3 className="text-base font-medium mb-2 group-hover:text-pink-500 transition-colors">
                    {product.name}
                  </h3>
                  <div className="flex justify-between items-center">
                    <p className="font-bold text-gray-900">{product.price} ₽</p>
                    <motion.button
                      whileHover={{ scale: 1.1 }}
                      whileTap={{ scale: 0.9 }}
                      className="bg-pink-500 text-white h-8 w-8 rounded-full flex items-center justify-center"
                    >
                      <span className="sr-only">Добавить в корзину</span>
                      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" className="w-5 h-5">
                        <path d="M12 4.5v15m7.5-7.5h-15" />
                      </svg>
                    </motion.button>
                  </div>
                </div>
              </Link>
            </motion.div>
          ))}
        </motion.div>
      </AnimatedContentSection>
    </div>
  );
} 