'use client';

import { ReactNode } from 'react';
import { motion } from 'framer-motion';
import { LucideIcon } from 'lucide-react';

interface AnimatedPageHeaderProps {
  title: string;
  description: string;
  icon?: LucideIcon;
  date?: string;
  bgColor?: string;
  iconColor?: string;
  children?: ReactNode;
}

export default function AnimatedPageHeader({
  title,
  description,
  icon: Icon,
  date,
  bgColor = 'bg-blue-100',
  iconColor = 'text-blue-500',
  children
}: AnimatedPageHeaderProps) {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        delay: 0.1,
        when: "beforeChildren",
        staggerChildren: 0.1
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: { 
      y: 0, 
      opacity: 1, 
      transition: { type: "spring", stiffness: 300, damping: 24 }
    }
  };

  return (
    <motion.section 
      className="text-center mb-16 relative overflow-hidden"
      initial="hidden"
      animate="visible"
      variants={containerVariants}
    >
      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden">
        <motion.div
          className="absolute top-10 right-10 w-20 h-20 bg-pink-500/20 rounded-full blur-xl"
          animate={{
            y: [0, -20, 0],
            scale: [1, 1.1, 1],
          }}
          transition={{
            duration: 4,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
        <motion.div
          className="absolute bottom-40 left-20 w-32 h-32 bg-pink-500/15 rounded-full blur-2xl"
          animate={{
            y: [0, 30, 0],
            x: [0, -10, 0],
            scale: [1, 0.9, 1],
          }}
          transition={{
            duration: 6,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
        <motion.div
          className="absolute top-40 left-1/4 w-16 h-16 bg-pink-500/25 rounded-full blur-lg"
          animate={{
            y: [0, -40, 0],
            x: [0, 20, 0],
          }}
          transition={{
            duration: 5,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
      </div>

      {/* Icon */}
      {Icon && (
        <motion.div 
          className={`w-16 h-16 ${bgColor} rounded-full flex items-center justify-center mx-auto mb-6 relative z-10`}
          variants={itemVariants}
          whileHover={{ 
            scale: 1.1, 
            rotate: [0, -5, 5, -5, 0],
            transition: { duration: 0.5 } 
          }}
        >
          <Icon className={`h-8 w-8 ${iconColor}`} />
        </motion.div>
      )}

      {/* Title */}
      <motion.h1 
        className="text-4xl md:text-5xl font-bold text-gray-900 mb-6 relative z-10"
        variants={itemVariants}
      >
        {title}
      </motion.h1>

      {/* Description */}
      <motion.p 
        className="text-xl text-gray-600 max-w-3xl mx-auto relative z-10"
        variants={itemVariants}
      >
        {description}
      </motion.p>

      {/* Date */}
      {date && (
        <motion.p 
          className="text-sm text-gray-500 mt-4 relative z-10"
          variants={itemVariants}
        >
          {date}
        </motion.p>
      )}

      {/* Additional content */}
      {children && (
        <motion.div 
          variants={itemVariants}
          className="mt-6 relative z-10"
        >
          {children}
        </motion.div>
      )}
    </motion.section>
  );
} 